.node {
    width: 35px;
    height: 35px;
    outline: 1px solid rgb(134, 134, 134);
    display: inline-block;
    border-radius: 7px;
    background-color:rgba(255, 255, 255, 0.4);

  }
  
  .node-finish {
    background-color: #ff6666;
  }
  
  .node-start {
    background-color: #6eccff;
  }
  
  .color {
    background-color: #70a7ff;
  }

  .node-visited {
    animation-name: visitedAnimation;
    animation-duration: 1.5s;
    animation-timing-function: ease-in-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes visitedAnimation {
    0% {
      transform: scale(0.3);
      /* background-color: rgba(0, 0, 66, 0.75); */
      background-color: #6176ff;
      border-radius: 100%;
    }
  
    50% {
      /* background-color: rgba(217, 17, 187, 0.75); */
      background-color: #70a7ff;
    }
  
    75% {
      transform: scale(0.9);
      /* background-color: rgba(34, 17, 217, 0.75); */
      background-color: #82e7df;
    }
  
    100% {
      transform: scale(1);
      /* background-color: #7bd385; */
      background-color: #7affad;
    }
  }
  
  .node-wall {
    background-color: #082032;
  }
  
  .node-shortest-path {
    animation-name: shortestPath;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
  }
  
  @keyframes shortestPath {
    0% {
      transform: scale(0.6);
      background-color: rgb(255, 254, 106);
    }
  
    50% {
      transform: scale(1);
      background-color: rgb(255, 254, 106);
    }
  
    100% {
      transform: scale(1);
      /* background-color: rgb(255, 254, 106); */
      background-color: #fcd080;
    }
  }
  