.grid-container {
    margin: auto;
    margin-top: 10px;
    margin-bottom: 20px;
  }
  
  .grid {
    white-space: pre;
  }
  
  button {
    margin: 2px 5px;
  }

  .navbar-custom {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 0.5rem 1rem;
}
.nav-title{
  font-size: 40px; 
  line-height: inherit;
  white-space: nowrap;
}

/* Button  */

.btn-custom {
  background-color: white;
  
  /* color: white;  */
  display: inline-block;
  text-align: center;
  vertical-align: middle;

  user-select: none;
  border: none;
  padding: 6px 15px;
  font-size: 17px;
  border-radius: 20px;
  box-shadow: 0px 2px 5px #8b8b8b;
}
.btn-custom:focus {
  outline: 1px solid black;
}
.btn-custom:hover {
  font-size: 18px;
  cursor: pointer;
  text-decoration: none;
}

.btn-clear-grid {
  background-color: #fcd080;
}
.btn-clear-walls {
  background-color: #082032;
  color: white;
}
.btn-astar {
  background-color: #FFC9B5;
}
.btn-dik {
  background-color: #F7B1AB;
}
.btn-bfs {
  background-color: #C7D3BF;
}
.btn-dfs {
  background-color: #93caa6;
}
.btn-git {
  background-color: #171515;
  color: white;
}

